.banner{
    position: fixed;
    width: 100%;
    margin-top: 0px;
    margin-bottom: 0px;
    bottom: 0px;
    background-color: transparent;
    background-image: linear-gradient(120deg, #43AEA7 0%, #37AA3C 100%);
}

.ticker-item {
    display: inline-block;
    padding: 0 20rem;
    font-family: "Josefin Sans", Sans-Serif;
    color: #ffffff;
    font-size: 20px;
}

.ticker-wrap {
    font-family: "Nunito";
    width: 100%;
    padding-left: 50%;
    overflow-x: hidden !important;
}

.ticker-move {
    display: inline-block;
    white-space: nowrap;
    padding-right: 50%;
    animation-iteration-count: infinite;
    animation-timing-function: linear;
    animation-name: ticker;
    animation-duration: 30s;
}

@keyframes ticker {
    0% { transform: translate3d(0, 0, 0); }
    100% { transform: translate3d(-130%, 0, 0); }
  }