.container{
    padding: 26px 50px 80px 50px;
    position: relative;

}

.modular{
    display: flex;
    margin-right: auto;
    margin-left:auto;
    position:relative;
}

.modular-row{
    width:100%;
    align-content:center;
    align-items: center;
}

.modular-title{
    text-align: center;
    margin-bottom: 20px;
    padding:25px 0px 0px 0px;
    width:100%;
}

.modular-title-mobile{
    text-align: center;
    margin-bottom: 20px;
    padding:25px 0px 0px 0px;
    width:100%;
}

.modular-title-mobile h1 {
    color: #1A4759;
    font-size: 23px;
    font-weight: 500;
}

.modular-title h1 {
    color: #1A4759;
    font-size: 36px;
    font-weight: 500;
}


.modularParaMobile{
    margin-top:35px;

}

.modularParaMobile h3 {
    color:#43AEAA;
    padding:0;
    margin:0;
    line-height: 1;
    font-size:20px;
    text-align: center;
}


.modularPara h3{
    color:#43AEAA;
    padding:0;
    margin:0;
    line-height: 1;
    font-size:24px;
    text-align: center;
}

.imageBox-module{
    text-align: center;
    margin-top:20px;
}

.imageBox-module img {
    width:42%;
    vertical-align: middle;
    display: inline-block;
    height:auto;
    border:none;
    box-shadow: none;
    max-width: 100%;
}


.imageBox-module-mobile{
    text-align: center;
    margin-top:65px;
}

.imageBox-module-mobile img {
    width:100%;
    vertical-align: middle;
    display: inline-block;
    height:auto;
    border:none;
    box-shadow: none;
    max-width: 100%;
}