.whyUsContainer{
    transition: background 0.3s, border 0.3s, border-radius 0.3s, box-shadow 0.3s;
    margin-top: 0px;
    margin-bottom: 0px;
    padding: 50px 0px 130px 0px;
    background-color: #031420;
    height: 150px;
    opacity: 0.9;
}

.whyUsContainerMobile{
    transition: background 0.3s, border 0.3s, border-radius 0.3s, box-shadow 0.3s;
    margin-top: 0px;
    margin-bottom: 0px;
    padding: 50px 0px 130px 0px;
    background-color: #031420;
    height: 230px;
    opacity: 0.9;
}

.whyUsTitle{
    text-align: center;
}

.whyUsTitleMobile{
    text-align: center;
    position: relative;
    top: -30px;
}

.whyUsTitleMobile h2{
    color:#43AEA7;
    font-size: 25px;
    font-weight: 500;
}

.whyUsTitle h2{
    color:#43AEA7;
    font-size: 30px;
    font-weight: 500;
}

.article{
    color: white;
    text-align: left;
    font-size: 20px;
    font-weight: normal;
    padding-left: 45px;
    padding-right: 45px;
}

.article-mobile{
    color: white;
    text-align: center;
    font-size: 17px;
    font-weight: normal;
    padding-left: 15px;
    padding-right: 15px;
}

.btn-readme {
    text-align: left;
    margin-right: 70px;
    padding-left: 45px;

}

.moreless-button {
  
    color: #37aa3cff !important;
    cursor: pointer;
}