.path{
    stroke: rgb(55, 170, 60);
    stroke-width: 9;
    fill: none;
    stroke-dasharray: 1500;
    stroke-dashoffset: 1500;
    /* animation: dash 5s linear forwards; */
    animation-duration: 10s;
    animation-timing-function: ease;
    animation-delay: 0s;
    animation-iteration-count: infinite;
    animation-direction: normal;
    animation-fill-mode: none;
    animation-play-state: running;
    animation-name: dash;
}

@keyframes dash {
    to {
        stroke-dashoffset: 0;
    }
  }

  .text-center {
    text-align: center;
}


.card{
    opacity: .8;
    position: relative;
    max-width: 330px;
    height: 270px;
    border-radius: 15px;
    padding:40px 15px;
    box-shadow: 0 10px 15px rgb(0 0 0 / 10%);
    transition: .5s;
    margin: 0 auto 10px;
    background: linear-gradient(to left,#43aea7ff,#43aea7ff 50%)!important;
}

.card:hover{
    box-shadow: 0 10px 15px rgb(0 0 0 / 10%);
    transform: scale(1.05);
    transition: .5s;
    height: 300px;
   
}


