.newsContainer{
    background-color: #E6FCFF;
    transition: background 0.3s, border 0.3s, border-radius 0.3s, box-shadow 0.3s;
    padding: 60px 0px 170px 0px;

}

.newsTitle{
    text-align: center;
    padding-bottom: 50px;
}

.newsTitleMobile{
    text-align: center;
    padding-bottom: 20px;
}

.newsTitleMobile h2 {
    color: #1A4759;
    font-size: 22px;
    font-weight: 500;
}
.newsTitle h2{
    color:#1A4759;
    font-size: 30px;
    font-weight: 500;
}
.newsCard{
    background-color: #fff;
    transition: background 0.3s, border 0.3s, border-radius 0.3s, box-shadow 0.3s;
    margin: 0px 20px 0px 20px;
    margin-right: 25px;
    margin-left: 25px;
    border-radius: 30px 30px 30px 30px;
    padding-top: 20px;
    padding-bottom: 20px;

}

.newsCardMobile{
    background-color: #fff;
    transition: background 0.3s, border 0.3s, border-radius 0.3s, box-shadow 0.3s;
    margin: 0px 20px 0px 20px;
    margin-right: 25px;
    margin-left: 25px;
    border-radius: 30px 30px 30px 30px;
    padding-top: 20px;
    padding-bottom: 20px;
    margin-bottom:20px;

}
.newsDate{
    color: rgb(67, 174, 170);
    position: relative;
    left: 25px;
    
}



.newsPara{
    padding-left: 25px;
    padding-right: 25px;
}

.newsPara:hover h3{
    color: rgb(67, 174, 170);
}

.newsPara h3{
    color:#09445A;
    font-size: 22px;
    line-height: 1.4em;
}

.newsPara a{
    color: inherit;
    font-size: inherit;
    line-height: inherit;
    font-weight: 500;
    text-decoration: none !important;
}

.newsParaMobile a {
    color: inherit;
    font-size: inherit;
    line-height: inherit;
    font-weight: 500;
    text-decoration: none !important;
}

.newsParaMobile{
    padding-left: 25px;
    padding-right: 25px;
}

.newsParaMobile h3 {
    color:#09445A;
    font-size: 16px;
    line-height: 1.4em;
}

.newsParaMobile:hover{
    color: rgb(67, 174, 170);
}