  .navLink:after,
.navLink:before {
  transition: all .5s;
}

.navLink,
.navLink {
  position: relative;
}
.navLink:after,
 .navLink:after {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
  width: 0%;
  color: #43aea7;
  background: #43aea7;
  height: 2px;
}
.navLink:hover:after {
  width: 100%;
}

.navLink {
  transition: all 2s;
}

 .navLink:after {
  text-align: left;
  content: '';
  margin: 0;
  opacity: 0;
}
 .navLink:hover {
  color: #43aea7;
  z-index: 1;
}
.navLink:hover:after {
  z-index: -10;
  animation: fill 1s forwards;
  -webkit-animation: fill 1s forwards;
  -moz-animation: fill 1s forwards;
  opacity: 1;
}

a:hover{
